// primary container
.About {
    padding: 10vh 5vw;
}

// About me division
.about-me {
    padding: .5cm;
    color: rgb(186, 183, 183);
    border: .5px solid rgb(41, 40, 40);
    // font-family: roboto;
    font-size: 16px;
    background-color: #1e1e1e;
    // background-color: #12121abc;
    border-radius: 3px;
    display: flex;
    flex-direction: column;
    row-gap: 1cm;
    // box-shadow: rgba(122, 121, 121, 0.16) 0px 2px 3px, rgba(61, 60, 60, 0.23) 0px 2px 3px;
	box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;


}

h2{
    // font-family: bree serif;
    margin: 0px 0px;
    font-weight: 500;
}
// primary about container
.about-me-container {
    padding: 5vh 0vw;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    column-gap: 1.5cm;
}

// click to go to skill page
.skill-click {
    color: rgba(110, 144, 245, 0.921);

}

.skill-click:hover {
    color: rgba(74, 116, 242, 0.921);
}

// my photo
.my-pic {
    width: 7cm;
    height: 7cm;
    border-radius: 500px;
    border: .5px solid rgb(41, 41, 41);
}

@media screen and (max-width:1200px) {
    .my-pic {
        width: 6cm;
        height: 6cm;
    }
    .about-me-container{
        column-gap: 1cm;
    }
}
@media screen and (max-width:1000px) {
    .my-pic {
        width: 5cm;
        height: 5cm;
    }
   
}
@media screen and (max-width:900px){
    .about-me-container{
        flex-direction: column;
        justify-content: center;
        align-items: center;
        row-gap: .5cm;
    }
    
}
@media screen and (max-width:600px){
    .about-me{
        font-size: 16px;
    }
    h2{
        font-size: 22px;
    }
}
@media screen and (max-width:500px){
    .about-me{
        width: 90vw;
    }
}
@media screen and (max-width:400px){
    .about-me{
        font-size: 15px;
    }
    h2{
        font-size: 20px;
    }
    .my-pic{
        width: 4cm;
        height: 4cm;
    }
}
@media screen and (max-width:300px){
    .about-me{
        font-size: 14px;
    }
    h2{
        font-size: 18px;
    }
}