// primary container
.Skills {
    padding: 10vh 0vw;
}

// skills container
.skills-container {
    padding-top: 5vh;
    display: flex;
    justify-content: center;
    column-gap: .6cm;
    row-gap: .6cm;
    flex-wrap: wrap;
}

// skill card
.skill {
    border: .5px solid #636367;
    width: 3.3cm;
    padding: .3cm;
    background-color: #1e1e1e;
    border-radius: 3px;
    // box-shadow: rgba(122, 121, 121, 0.16) 0px 2px 3px, rgba(61, 60, 60, 0.23) 0px 2px 3px;
	box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border: .1px solid rgb(50, 49, 49);
}

// image of skill
.skill-img {
    width: 1.5cm;
    padding: .1cm;
}

// skill name
.skill-name {
    font-size: 16px;
    padding-top: .2cm;
    letter-spacing: .025cm;
    color: rgba(197, 193, 193, 0.951);
    font-family: raleway;
    font-weight: 500;
}

// skill proficiency
.proficiency {
    color: blueviolet;
    font-size: 18px;
}

@media screen and (max-width:1000px) {
    .skill-name {
        font-size: 16px;
    }

    .skill-img {
        width: 1.4cm;

    }
}

@media screen and (max-width:700px) {
    .skill-name {
        font-size: 16px;
    }
    .skill{
        width: 3cm;
    }

    .skill-img {
        width: 1.2cm;

    }
}