.Navbar{
    padding-top: .3cm;
}

// logo
.logo {
    width: 1.1cm;
}

// navigation bar's left division
.left-nav {
    display: flex;
    column-gap: .2cm;
    align-items: center;
}

// my name
.name{
    color: rgb(164, 161, 161);
    font-family: raleway;
    font-weight: 500;
    font-size: 25px;
    letter-spacing: .015cm;
}
.name:hover{
    color: rgb(243, 241, 241);
}
// navigation bar's menu item
.menuItem{
    color: rgb(182, 177, 177);
    cursor: pointer;
    font-size: 14px;
}
.menuItem:hover{
    color: rgb(243, 241, 241);
}
// navigation menu
.menu{
    display: flex;
    align-items: center;
    column-gap: .5cm;
}

// navigation menu container
.nav-container{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 5px 0px;
    border-bottom: 1px solid rgb(63, 62, 62);
}

// link of nav menu to component
.link-nav{
    text-decoration: none;
    margin: 0px 0px;
}



.overlay {
	height: 100%;
	width: 0%;
	position: fixed;
	z-index: 1;
	top: 0;
	right: 0;
	background-color: #3b3939;
	overflow-y: hidden;
	transition: 0.5s;
}
.mt{
    color: rgb(236, 234, 234);
}


.overlay-content {
	position: relative;
	top: 15%;
	width: 100%;
	text-align: center;
	margin-top: 30px;
}

.overlay p {
	padding: 8px;
	text-decoration: none;
	font-size: 18px;
	color: rgb(239, 5, 5);
	display: block;
	transition: 0.3s;
	font-family: Fira Sans;
}

.overlay p:hover,
.overlay p:focus {
	color: #f1f1f1;
}

.overlay .closebtn {
	position: absolute;
	top: 10px;
	right: 45px;
	font-size: 60px;
}


.menu-icon {
	height: .8cm;
	display: none;
	// box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.23) 0px 6px 6px;
	// border-radius: 10px;
}
@media screen and (max-width:900px){
    .name{
        font-size: 28px;
    }
    .logo{
        width: 1.1cm;
    }
}

@media screen and (max-width: 800px) {

	.menuItem{
        display: none;
    }
	.menu-icon {
		display: block;
		cursor: pointer;
	}
    
}
@media screen and (max-width:600px){
    .menu-icon{
        width: .65cm;
        height: .65cm;
    }
}
@media screen and (max-width:500px){
    .name{
        font-size: 22px;
    }
    .logo{
        width: 1cm;
    }
    
}
@media screen and (max-width:400px){
    .name{
        display: none;
    }
    .logo{
        width: 1cm;
    }
    
}