// home svg file
.coder-svg {
    width: 10cm;
}

// home container primary 
.Home {
    padding: 0vw 5vw;
}

.wave-hand{
    rotate: 90deg;
}

// home container
.home-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    column-gap: 10vw;
    padding: 10vh 0vh;
}

.bottom-link{
    color: gray;
    font-size: 18px;
    text-align:center;
}
// tagline home
.tagline {
    // color: rgba(197, 193, 193, 0.951);
    color: rgba(197, 193, 193, 0.951);

    // color: white;
    font-size: 70px;
    // font-weight: 600;
    letter-spacing: .05cm;
    font-family: bree serif;
    width: fit-content;

}

.tagline:hover {
    background-color: rgb(36, 115, 70);
    color: whitesmoke;
}

// subscribe button
.subscribe {
    font-size: 18px;
    padding: .2cm;
    border: none;
    border-radius: 3px;
    cursor: pointer;
    // color: rgba(150, 175, 250, 0.921);
    background-color: rgba(162, 184, 251, 0.921);
}

.subscribe:hover {
    background-color: rgba(142, 170, 255, 0.921);
}

// sub tagline home
.sub-tagline {
    font-size: 35px;
    color: rgba(143, 171, 253, 0.921);
    font-family: inika;
    // font-weight: 600;
    letter-spacing: .02cm;
}

// know more button
.know-more {
    background-color: #77dd77; 
    padding: 5px 10px;
    font-size: 18px;
    // font-weight: 600;
    border: none;
    border-radius: 3px;
    box-shadow: inset 0 0 0.5px 1px hsla(0, 0%,
            100%, 0.075),
        /* shadow ring ðŸ‘‡ */
        0 0 0 1px hsla(0, 0%, 0%, 0.05),
        /* multiple soft shadows ðŸ‘‡ */
        0 0.3px 0.4px hsla(0, 0%, 0%, 0.02),
        0 0.9px 1.5px hsla(0, 0%, 0%, 0.045),
        0 3.5px 6px hsla(0, 0%, 0%, 0.09);

    cursor: pointer;

}

.know-more:hover {
    color: black;
    background-color: #6bbd6b;
}

// general headings
.headingg {
    color: rgba(175, 175, 175, 0.848);
    // color: rgb(21, 138, 97);
    font-family: bree serif;
    letter-spacing: .025cm;
    font-size: 35px;
    text-decoration: underline;
}

// latest blog container home
.latest-blogs-container {
    padding-bottom: 10vh;
    padding-top: 2vh;
}

// latest blog details homepage
.blog-details-home {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 2vh 2vw;
    border-bottom: .5px solid rgb(54, 53, 53);


}

// waving hand emoji
.wave-hand {
    transform: rotate(90deg);
}

.blog-name-home { 
    color: rgb(184, 181, 181);
    // color: rgba(197, 193, 193, 0.951);
    font-size: 21px;
    // font-weight: 600;
    letter-spacing: .02cm;
    font-family: roboto;
    cursor: pointer;
}

.news-tag {
    color: rgb(184, 181, 181);
    // color: rgba(197, 193, 193, 0.951);
    font-size: 18px;
    // font-weight: 600;
    letter-spacing: .02cm;
    font-family: roboto;

}

.blog-date-home {
    color: rgb(121, 121, 121);
    // font-size: 18px;
    // font-weight: 600;
    letter-spacing: .03cm;
    font-family: roboto;
    font-size: 14px;
}

.blog-name-home:hover {
    color: rgb(135, 135, 252);
}

// latest blog name container homepage
.blog-name-container-home {
    display: flex;
    align-items: baseline;
    column-gap: .1cm;
}


// redirect icon
.redirect {
    width: .27cm;
}

// show more line
.show-more {
    padding-left: 2vw;
    cursor: pointer;
    color: rgb(148, 148, 255);
}

.show-more:hover {

    color: rgb(105, 105, 251);
    // text-decoration: underline;

}

@media screen and (max-width:1300px) {
    .coder-svg {
        width: 8cm;
    }

    .tagline {
        font-size: 65px;
    }

    .sub-tagline {
        font-size: 30px;
    }
}

@media screen and (max-width:1100px) {
    .home-container {
        flex-direction: column-reverse;
        // justify-content: flex-start;
        row-gap: 1cm;
    }
}

@media screen and (max-width:900px) {
    .blog-name-home {
        font-size: 19px;
    }

    .blog-date-home {
        font-size: 13px;
        font-weight: 500;
    }
}

@media screen and (max-width:700px) {
    .coder-svg {
        width: 8cm;
    }

    .tagline {
        font-size: 60px;
    }

    .sub-tagline {
        font-size: 25px;
    }

    .blog-name-home {
        font-size: 18px;
    }

    .blog-date-home {
        font-size: 12px;
        font-weight: 500;
    }

    .headingg {
        font-size: 30px;
    }

    .redirect {
        width: .24cm;
    }
}

@media screen and (max-width:600px) {
    .coder-svg {
        width: 6cm;
    }

    .tagline {
        font-size: 50px;
    }

    .sub-tagline {
        font-size: 22px;
    }

    .know-more {
        font-size: 16px;
    }

    .redirect {
        width: .22cm;
    }
    .show-more{
        font-size: 14px;
    }
    .subscribe{
        font-size: 17px;
    }
}

@media screen and (max-width:500px) {
    .coder-svg {
        width: 6cm;
    }

    .tagline {
        font-size: 43px;
    }

    .sub-tagline {
        font-size: 20px;
    }

    .know-more {
        font-size: 16px;
        padding: 4px 7px;
    }

    .blog-date-home {
        font-size: 11px;
    }


    .redirect {
        width: .2cm;
    }
    .subscribe{
        font-size: 16px;
        padding: .2cm;
    }
}

@media screen and (max-width:400px) {
    .coder-svg {
        width: 5cm;
    }

    .tagline {
        font-size: 35px;
    }

    .sub-tagline {
        font-size: 19px;
    }

    .blog-date-home {
        font-size: 10px;
    }

    .know-more {
        font-size: 15px;
        padding: 4px 7px;
    }

    .blog-name-home {
        font-size: 15px;
    }

    .headingg {
        font-size: 26px;
    }
    .show-more{
        font-size: 12px;
    }
    .subscribe{
        font-size: 14px;
        padding: .2cm;
    }
}

@media screen and (max-width:300px) {
    .blog-details-home {
        flex-direction: column;

        align-items: flex-start;
        row-gap: .15cm;
    }
    .latest-blogs-container{
        width: fit-content;
    }
    .show-more{
        font-size: 11px;
    }
    
}