body{
    background-color: #16161d;
}
.App{
    padding: 0vw 5vw;
}
p{
    margin: 0px 0px;
}
h1,h2,li{
    margin: 0px 0px;
}
a{
  text-decoration: none;
}

::-webkit-scrollbar {
    width: 10px;
    height: 0px;
    transition: .3s;
    z-index: 10;
  }
  
  
  
  
  ::-webkit-scrollbar-track {
    background: #16161d; 
  }
   
  
  ::-webkit-scrollbar-thumb {
    background: rgb(64, 63, 63); 
  }
  
  
  ::-webkit-scrollbar-thumb:hover {
    background: rgb(51, 50, 50); 
 
  }
  