// Primary container
.Projects {
    padding-top: 10vh;
}

// projects container
.projects-container {
    padding: 5vh 0vh;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    column-gap: .6cm;
    row-gap: .6cm;
}

// project box
.project {
    border: .1px solid rgb(50, 49, 49);

    width: 8cm;
    height: auto;
    padding: 0.5cm;
    background-color: #1e1e1e;
    border-radius: 3px;
    // box-shadow: rgba(122, 121, 121, 0.16) 0px 2px 3px, rgba(61, 60, 60, 0.23) 0px 2px 3px;
	box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;


}

// project logo and name container 
.project-logo-container {
    display: flex;
    align-items: center;
    column-gap: .3cm;
}

.project-year {
    padding-left: 1.6cm;
    color: blueviolet;
    font-size: 14px;
}

.project-logo {
    width: 1.3cm;
}

.project-name {
    font-size: 24px;
    letter-spacing: .005cm;
    color: rgba(197, 193, 193, 0.951);
    font-family: fira sans;

}

.project-tagline {
    font-size: 16px;
    letter-spacing: .02cm;
    color: rgb(146, 144, 144);
    font-family: raleway;
}

// container of button redirect to source code of project
.code {
    display: flex;
    align-items: baseline;
    column-gap: .08cm;
    border: .5px solid rgb(63, 63, 63);
    width: fit-content;
    padding: 1px 5px;
    border-radius: 3px;
    // background-color: #16161d;
    // background-color: #2c2b2b;

    // box-shadow: rgba(122, 121, 121, 0.16) 0px 2px 3px, rgba(61, 60, 60, 0.23) 0px 2px 3px;
	// box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;



}

// primary container of buttons
.project-buttons {
    // margin-left: 2cm;
    display: flex;
    align-items: center;
    justify-content: center;
    column-gap: .8cm;
    flex-wrap: wrap;
    row-gap: .5cm;

}

// button redirect to source code of project
.code-link {
    color:rgb(160, 160, 160);
    cursor: pointer;
    font-size: 15px;
    padding: .02cm 0.01cm;
}

.code-link:hover {
    text-decoration: underline;

}

