.timeline-container{
    width: 80vw;
    padding: 5vh 0vw;
    
}

* {
    box-sizing: border-box;
  }
  
  /* Set a background color */
  // body {
  //   background-color: #474e5d;
  //   font-family: Helvetica, sans-serif;
  // }
  
  /* The actual timeline (the vertical ruler) */
  .timeline {
    position: relative;
    max-width: 1100px;
    margin: 0 auto;
    // border: 1px solid gray;
  }
  
  /* The actual timeline (the vertical ruler) */
  .timeline::after {
    content: '';
    position: absolute;
    width: 6px;
    background-color: #727272;
    top: 0;
    bottom: 0;
    left: 50%;
    margin-left: -3px;
  }
  
  /* Container around content */
  .container {
    padding: 10px 40px;
    position: relative;
    background-color: inherit;
    width: 50%;
    
  }
 
  
  /* The circles on the timeline */
  .container::after {
    content: '';
    position: absolute;
    width: 25px;
    height: 25px;
    right: -17px;
    background-color: #1c1b1b;
    border: 4px solid rgb(149, 149, 150);
    top: 15px;
    border-radius: 50%;
    z-index: 1;
  }
  
  /* Place the container to the left */
  .left {
    left: 0;
  }
  
  /* Place the container to the right */
  .right {
    left: 50%;
  }
  
  /* Add arrows to the left container (pointing right) */
  .left::before {
    content: " ";
    height: 0;
    position: absolute;
    top: 22px;
    width: 0;
    z-index: 1;
    right: 30px;
    border: medium solid #737272;
    border-width: 10px 0 10px 10px;
    border-color: transparent transparent transparent #737272;
  }
  
  /* Add arrows to the right container (pointing left) */
  .right::before {
    content: " ";
    height: 0;
    position: absolute;
    top: 22px;
    width: 0;
    z-index: 1;
    left: 30px;
    border: medium solid #737272;
    border-width: 10px 10px 10px 0;
    border-color: transparent #737272 transparent transparent;
  }
  
  /* Fix the circle for containers on the right side */
  .right::after {
    left: -16px;
  }
  
  /* The actual content */
  .content {
    padding: 5px 20px 10px 20px;
    background-color:  #1e1e1e;
    border: .1px solid rgb(50, 49, 49);
    box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;


    position: relative;
    border-radius: 6px;
    
    overflow-wrap: break-word;
    font-family: raleway;

    p{
      margin: 5px 0px;
      
    }
   
    
  }
  .content:hover{
    // box-shadow: -1px -1px 5px rgba(255,255,255,0.45), 5px 5px 9px rgba(94,104,121,0.3);
	box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;

    cursor: pointer;
  }
  .timelineText{
    font-size: 23px;
    color: rgba(197, 193, 193, 0.951);

    // font-weight: 600;
  }
  .timelineYear{
    font-size: 12px;
    // font-weight: 600;
    color: rgb(136, 137, 138);
    font-family: roboto;
  }
  .timelineTagLine{
    // font-weight: 600;
    font-size: 14px;
    font-family: roboto;
    color: rgb(101, 135, 174);
  }

  .downCV{
    border: none;
    color: #28282b;
    background-color: #6ed46e;
    // color: whitesmoke;
    cursor: pointer;
    transition:.5s;
    width: 5cm;
    height: 2cm;
    font-size: 35px;
    // box-shadow: -1px -1px 5px rgba(255,255,255,0.45), 5px 5px 9px rgba(94,104,121,0.3);
	box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;

    font-family: Ubuntu;
    font-weight: 600; 
    border-radius: 10px;
    transition: .1s;
  }
  .downCV:hover{
    background-color: rgb(75, 199, 75);
  }
  
  /* Media queries - Responsive timeline on screens less than 600px wide */
  @media screen and (max-width: 950px) {
  /* Place the timelime to the left */
    .timeline::after {
      left: 31px;
    }
    .downCV{
      width: 5cm;
      height: 1.8cm;
      font-size: 33px;
    }
  
  /* Full-width containers */
    .container {
      width: 100%;
      padding-left: 70px;
      padding-right: 25px;
    }
  
  /* Make sure that all arrows are pointing leftwards */
    .container::before {
      left: 60px;
      border: medium solid #9fc5d0;
      border-width: 10px 10px 10px 0;
      border-color: transparent #9fc5d0 transparent transparent;
    }
  
  /* Make sure all circles are at the same spot */
    .left::after, .right::after {
      left: 15px;
    }
  
  /* Make all right containers behave like the left ones */
    .right {
      left: 0%;
    }
  }

  @media screen and (max-width:800px){
    .timelineText{
      font-size: 21px;
    }
    .timelineTagLine{
      font-size: 14px;
    }
    .timelineYear{
      font-size: 12px;
    }
    .downCV{
      width: 4.8cm;
      height: 1.4cm;
      font-size: 31px;
    }
    
  }

  @media screen and (max-width:700px){
    .timelineText{
      font-size: 20px;
    }
    .timelineTagLine{
      font-size: 13px;
    }
    .timelineYear{
      font-size: 11px;
    }
    .downCV{
      width: 4.5cm;
      height: 1.2cm;
      font-size: 29px;
    }
    
  }

  @media screen and (max-width:550px){
    .timelineText{
      font-size: 20px;
    }
    .timelineTagLine{
      font-size: 12.5px;
    }
    .timelineYear{
      font-size: 11px;
    }
    .downCV{
      width: 4cm;
      height: 1.2cm;
      font-size: 27px;
    }
    
  }
  @media screen and (max-width:450px){
    .timelineText{
      font-size: 19px;
    }
    .timelineTagLine{
      font-size: 12px;
    }
    .downCV{
      width:3.5cm;
      height: 1.2cm;
      font-size: 25px;
    }
  }
  @media screen and (max-width:350px){
    .timelineText{
      font-size: 18px;
    }
    .timelineTagLine{
      font-size: 11px;
    }
    .downCV{
      width: 3cm;
      height: 1.1cm;
      font-size: 19px;
    }
  }
  @media screen and (max-width:250px){
    .timelineText{
      font-size: 17px;
    }
    .timelineTagLine{
      font-size: 10px;
    }
    .timelineYear{
      font-size: 10px;
    }
    .downCV{
      width: 2.5cm;
      height: 1cm;
      font-size: 17px;
    }
    
  }