.Contact {
    padding: 10vh 5vw;
}

// contact box primary container
.contact-box-container {
    padding-top: 5vh;
    display: flex;
    justify-content: center;
}

// social icons container
.social-icons {
    display: flex;
    column-gap: .35cm;
    flex-wrap: wrap;
    row-gap: .3cm;
}

// social icons
.social-icon {
    width: .8cm;
    height: .8cm;
}

.sub-search {
    color: rgb(184, 183, 183);
}

// contact box container
.contact-container {
    border: .1px solid rgb(50, 49, 49);
    display: flex;
    flex-direction: column;
    align-items: center;
    width: fit-content;
    height: auto;
    padding: .8cm;
    background-color: #1e1e1e;
    border-radius: 3px;
    // box-shadow: rgba(122, 121, 121, 0.16) 0px 2px 3px, rgba(61, 60, 60, 0.23) 0px 2px 3px;
	box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;

}

// contact form
.contact-form {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    row-gap: .5cm;
}

// inputs
.contact-inputs {
    width: 7cm;
    background-color: rgb(221, 232, 235);
    border: none;
    font-size: 14px;
    padding: .2cm;
    border-radius: 3px;
    // box-shadow: rgba(122, 121, 121, 0.16) 0px 2px 3px, rgba(61, 60, 60, 0.23) 0px 2px 3px;
	box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;

    font-family: roboto;

}

// contact form submit button
.submit-contact {
    // background-color: rgb(183, 117, 246);
    background-color: #77dd77;
    
    font-size: 14px;
    border: none;
    border-radius: 3px;
    padding: .1cm .2cm;
    cursor: pointer;
}

.submit-contact:hover {
    // background-color: rgb(171, 90, 246);
    color: black;
    background-color: #6bbd6b;


}

.email-at {
    color: rgb(197, 196, 196);
}


@media screen and (max-width:600px) {
    .contact-inputs {
        width: 7cm;
    }

    .social-icon {
        width: .7cm;
        height: .7cm;
    }

    .sub-search {
        font-size: 14px;
    }

    .email-at {
        font-size: 14px;
    }
    .contact-container{
        padding: .5cm;
    }
}
@media screen and (max-width:500px) {
    .contact-inputs {
        width: 65vw;
        font-size: 16px;
    }

    .social-icon {
        width: .65cm;
        height: .65cm;
    }
    .submit-contact{
        font-size: 16px;
    }
   
}
@media screen and (max-width:350px) {
    .contact-inputs {
        width: 60vw;
        font-size: 14px;
    }

    .social-icon {
        width: .65cm;
        height: .65cm;
    }
   
}
